<script setup>
import TextEditor from '@/components/TextEditor.vue';
import { onBeforeMount, ref } from "vue";
import About from "@/api/About";

const content = ref()
const id = ref()
const message = ref()

async function update() {

  const res = await About.update(id.value, { deskripsi: content.value })
  window.scrollTo({
    top: 0,
    left: 0,
    behavior: 'smooth'
  })

  message.value = res.message
}

onBeforeMount(async () => {
  const res = await About.all()
  content.value = res[0].deskripsi

  id.value = res[0]._id
})

</script>

<template>
  <h2>Edit Tentang Kami</h2>

  <v-divider></v-divider>

  <v-alert type="success" class="my-3" closable close-label="Close Alert" v-if="message">
    {{ message }}
  </v-alert>

  <div class="mt-4">
    <TextEditor v-model="content" />
  </div>

  <v-divider class="my-3"></v-divider>

  <div>
    <v-btn color="green" @click="update">Simpan</v-btn>
  </div>

</template>